<template>
  <component
    :is="component"
    :to="props.to"
    class="relative inline-block rounded-sm border text-center transition-opacity"
    :class="{
      'px-3 py-[0.5rem] leading-4': props.size === 'md',
      'px-4 py-2 text-lg': props.size === 'lg',
      'border-blue-600 text-inherit': props.variant === 'outlined',
      'border-blue-600 bg-blue-700 text-white': props.variant === 'primary',
      'border-red-400 bg-red-500 text-white': props.variant === 'danger',
      'pointer-events-none opacity-50': props.disabled,
    }"
    @click.capture="onClick"
  >
    <Transition>
      <div
        v-if="props.loading"
        class="absolute inset-0 flex items-center justify-center"
      >
        <v-spinner
          size="1.3em"
          thickness="0.2em"
          :class="{
            'border-white/30 border-l-white': props.variant === 'primary' || props.variant === 'danger',
            'border-blue-700/30 border-l-blue-700': props.variant === 'outlined',
          }"
        />
      </div>
    </Transition>
    <div
      :class="{
        'opacity-0': props.loading,
      }"
    >
      <slot />
    </div>
  </component>
</template>
<script setup lang="ts">
const props = withDefaults(
  defineProps<{
    to?: string
    variant?: "outlined" | "primary" | "danger"
    size?: "md" | "lg"
    loading?: boolean
    disabled?: boolean
  }>(),
  {
    variant: "outlined",
    size: "md",
  }
)

const component = computed(() => {
  if (props.to) {
    return resolveComponent("NuxtLink")
  }

  return "button"
})

function onClick(e: MouseEvent) {
  if (component.value === "button") {
    e.preventDefault()
  }

  if (props.loading) {
    e.preventDefault()
    e.stopImmediatePropagation()
  }
}
</script>
